<template>
  <div class="container">
    <div class="row">
      <div class="col-0 col-md-2"></div>
      <div class="col-12 col-md-8">
        <div>
          <h2>👉 THIÊT KẾ ĐẸP, HOÀN THIỆN TỐT</h2>
          <p>Với thiết kế vuông bo tròn 4 góc kết hợp với màn hình tràn viền ở mặt trước, S5 Smartwatch có thiết kế mặt
            giống với Apple Watch Series 5, chỉ khác là  chỉ có 1 nút bấm tròn phía trên. Mặt đồng hồ dạng tràn viền,
            sang trọng. Màu sắc đồng hồ có 2 tùy chọn là đen và trắng, phù hợp cho cả nam lẫn nữ. Chất liệu dây chống bám mồ
            hôi, chống bám bụi tốt. Người dùng cũng dễ dàng thay đổi sang dạng dây mình muốn khi dây của <b>Apple S5 Rep 1:1</b> Smartwatch có
            thể dùng chung với dây của Apple Watch Series 5.</p>
        </div>
        <div>
          <img src="../../../../assets/images/mausac.jpg" alt="" class="img-fluid img-full-width">
        </div>
        <div>
          <h2>👉 MÀN HÌNH TRÀN VIỀN, SẮC NÉT</h2>
          <p><b>Apple S5 Rep 1:1</b> Smartwatch có màn hình 1.75inch IPS, độ phân giải 320 x 385 pixels, cảm ứng mượt mà. Thiết kế dạng tràn
            viền cực đẹp, chi tiết hiển thị sắc nét, màu sắc rực rỡ ngay cả trong điều kiện ngoài trời. Kích thước mặt của
            <b>Apple S5 Rep 1:1</b> cũng to hơn so với các Smartwatch trong cùng tầm giá.</p>
        </div>
        <div>
          <img src="../../../../assets/images/manhinhlon.jpg" alt="" class="img-fluid img-full-width">
        </div>
        <div>
          <img src="../../../../assets/images/kichthuocsanpham.jpg" alt="" class="img-fluid img-full-width">
        </div>
        <p>Giao diện của <b>Apple S5 Rep 1:1</b> hiển thị dưới dạng icon hoặc dạng bong bóng hiện đại, thân thiện, dễ sử dụng. Một điểm cực kỳ
          đáng tiền của <b>Apple S5 Rep 1:1</b> so với các Smartwatch khác đó là máy được trang bị Full ngôn ngữ Tiếng Việt và không hề bị lỗi
          Font Tiếng Việt.</p>
        <div>
          <img src="../../../../assets/images/giaodienhiendai.jpg" alt="" class="img-fluid img-full-width">
        </div>
        <div>
          <h2>👉 TÍNH NĂNG NGHE GỌI CỰC CHẤT</h2>
          <p><b>Apple S5 Rep 1:1</b> Smartwatch là mẫu Smartwatch hiếm hoi trong tầm giá 500K có thể nghe gọi thông qua kết nối Bluetooth, một
            điều mà gần như không có Smartwatch nào trong tầm giá này làm được.</p>
        </div>
        <div>
          <img src="../../../../assets/images/nghegoitructiep.jpg" alt="" class="img-fluid img-full-width">
        </div>
        <div>
          <h2>👉 CHUẨN KHÁNG NƯỚC IP68</h2>
          <p><b>Apple S5 Rep 1:1</b> Smartwatch có khả năng chống bám bụi, bám bẩn. Ngoài ra đồng hồ còn được trang bị khả năng chống nước IP68,
            đảm bảo cho người dùng vô tư đi dưới trời mưa hay hoạt động thể thao mạnh ra nhiều mồ hôi mà không sợ đồng hồ bị
            vào nước</p>
        </div>
        <div>
          <img src="../../../../assets/images/chongnuoc.jpg" alt="" class="img-fluid img-full-width">
        </div>
        <div>
          <h2>👉 THỜI LƯỢNG PIN <b>Apple S5 Rep 1:1</b></h2>
          <p><b>Apple S5 Rep 1:1</b> Smartwatch chỉ mất hơn 1h để sạc đầy pin. Nếu bật tất cả thông báo cũng như sử dụng hết các tính năng của
            nó thì thời lượng pin của <b>Apple S5 Rep 1:1</b> ~ trụ được khoảng một ngày cho 1 lần sạc đầy với kết nối Bluetooth duy trì liên
            tục. Nếu nhu cầu sử dụng thông thường ít thông báo, <b>AppleWatch S5 Rep 1:1</b> cho thời lượng sử dụng ~2 ngày. Ngoài các tính năng kể
            trên thì <b>Apple S5 Rep 1:1</b> Smartwatch vẫn hỗ trợ những tính năng rảnh tay khác như máy tính, đồng hồ bấm giờ, báo thức… vô
            cùng tiện lợi khi sử dụng</p>
        </div>
        <div>
          <img src="../../../../assets/images/pin.jpg" alt="" class="img-fluid img-full-width">
        </div>
        <div>
          <h2>👉 CHẾ ĐỘ THEO DÕI SỨC KHOẺ</h2>
          <p>- Theo dõi sức khỏe: Đo nhịp tim, huyết áp, điện tâm đồ, Oxy trong máu. Đặc biệt <b>Apple S5 Rep 1:1</b> Smarrtwatch có tính năng
            đo nhiệt độ cơ thể, điều mà gần như không có Smarrtwatch nào trên thị trường hiện nay làm được.</p>
          <p>- Theo dõi luyện tập: Đếm bước chân, theo dõi 10 môn thể thao: Đi bộ, chạy, leo núi, đi xe đạp, đánh bóng bàn,
            cầu lông, bóng rổ, nhảy dây, đá bóng, yoga. Nhờ cảm biến thế hệ mới, các chế độ theo dõi sức khoẻ trên <b>Apple S5 Rep 1:1</b> cho
            ra kết quả khá chính xác từ đó đưa ra những gợi ý tập luyện khoa học dựa trên thói quen tập luyện thường ngày
            của người dùng.</p>
          <div>
            <img src="../../../../assets/images/nhiptim.jpg" alt="" class="img-fluid img-full-width">
          </div>
        </div>
      </div>
      <div class="col-0 col-md-2"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'feature-layout'
}
</script>
<style scoped>
.img-full-width {
  width: 100%;
  padding: 25px;
  max-width: unset !important;
}
</style>



