            <slide>
              <img src="../../../../assets/images/w3.jpg" class="img-fluid" alt="slide-w1">
            </slide>
            <slide>
              <img src="../../../../assets/images/w4.jpg" class="img-fluid" alt="slide-w1">
            </slide>
            <slide>
              <img src="../../../../assets/images/w5.jpg" class="img-fluid" alt="slide-w1">
            </slide>
            <slide>
              <img src="../../../../assets/images/w6.jpg" class="img-fluid" alt="slide-w1">
