<script>
// eslint-disable-next-line
/* eslint-disable */
import {Hooper, Slide} from 'hooper'
import 'hooper/dist/hooper.css'
import LayoutCountdown from '../countdown/countdow-layout'
import LayoutSale from '../sale/sale-layout'
export default {
  name: 'slide-layout',
