<style scoped>
.hooper {
  height: unset !important;
}

h5 {
  margin-top: 20px;
  text-align: center;
  color: rgb(36, 36, 36);
  font-size: 16px;
  line-height: 1.5;
}



small {
  color: #fd7e14;
  font-size: 14px;
  text-transform: uppercase;
}

#btnNhanuudai {
  display: table;
  margin: 20px auto;
  cursor: pointer;
  animation-name: pulse;
  animation-delay: 1s;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  border-radius: 17px;
  background: rgb(225, 65, 110);
}

p {
  padding: 10px 20px;
  font-family: "Josefin Sans", sans-serif;
  color: rgb(255, 255, 255);
  font-size: 26px;
  font-weight: bold;
  text-align: center;
  line-height: 1.6;
  display: table-cell;
  vertical-align: middle;
  width: 100%;
  background-size: cover;
  background-position: center center;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

</style>



