<template>
  <div>
    <ul class="vuejs-countdown">
      <li v-if="days > 0">
        <p class="digit">00</p>
        <p class="text">Ngày</p>
      </li>
      <li>
        <p class="digit">{{ hours | twoDigits }}</p>
        <p class="text">Giờ</p>
      </li>
      <li>
        <p class="digit">{{ minutes | twoDigits }}</p>
        <p class="text">Phút</p>
      </li>
      <li>
        <p class="digit">{{ seconds | twoDigits }}</p>
        <p class="text">Giây</p>
      </li>
    </ul>
  </div>
</template>

<script>
// eslint-disable-next-line
/* eslint-disable */
import Countdown from 'vuejs-countdown'
let interval = null;
export default {
  name: 'countdow-layout',
  components: {
    Countdown
  },
  props: {
    deadline: {
      type: String
    },
    end: {
      type: String
    },
    stop: {
      type: Boolean
    }
  },
  data() {
    return {
      now: Math.trunc((new Date()).getTime() / 1000),
      date: null,
      diff: 0,
      endTime: 'August 22, 2022'
    }
  },
  created() {
    this.date = Math.trunc(Date.parse(this.endTime.replace(/-/g, "/")) / 1000);
    if (!this.date) {
      throw new Error("Invalid props value, correct the 'deadline' or 'end'");
    }
    interval = setInterval(() => {
      this.now = Math.trunc((new Date()).getTime() / 1000);
    }, 1000);
  },
  computed: {
    seconds() {
      return Math.trunc(this.diff) % 60
    },
    minutes() {
      return Math.trunc(this.diff / 60) % 60
    },
    hours() {
      return Math.trunc(this.diff / 60 / 60) % 24
    },
    days() {
      return Math.trunc(this.diff / 60 / 60 / 24)
    }
  },
  watch: {
    now (value) {
      this.diff = this.date - this.now;
      if (this.diff <= 0 || this.stop) {
        this.diff = 0;
        // Remove interval
        clearInterval(interval);
      }
    }
  },
  filters: {
    twoDigits (value) {
      if (value.toString().length <= 1) {
        return '0' + value.toString()
      }
      return value.toString()
    }
  },
  destroyed () {
    clearInterval(interval);
  }
}
</script>

<style scoped>
div{
  margin: 0px 20px;
}
.vuejs-countdown {
  margin: 25px 0px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-left: unset;
  border:3px solid black;
}
.vuejs-countdown li {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 25%;
  margin: 10px 0px;

}
.vuejs-countdown .digit {
  font-size: 32px;
  font-weight: 600;
  line-height: 1.4;
  margin-bottom: 0;
  color: black;
  font-weight: 400;
}
.vuejs-countdown .text {
  color: gray;
  font-size: 18px;
  font-weight: 400;
  text-transform: uppercase;
  margin-bottom: 0;
}
</style>



