<template>
  <section>
    <div class="container">
      <div class="row">
        <div class="col-12 col-md-3"></div>
        <div class="col-12 col-md-6">
          <article id="formCustomer">
            <sale-layout/>
            <countdow-layout/>
            <h2>ĐẶT MUA NGAY</h2>
            <div class="form-group">
              <input type="text"
                     id="name"
                     class="form-control"
                     v-model="userData.name" placeholder="Họ và tên">
              <br>
              <input type="number"
                     id="phone"
                     class="form-control"
                     v-model="userData.phone" placeholder="Số điện thoại">
              <br>
              <input type="text"
                     id="adress"
                     class="form-control"
                     v-model="userData.adress" placeholder="Địa chỉ">
              <br>
              <input type="number"
                     id="count"
                     class="form-control"
                     v-model="userData.count" placeholder="Số lượng">
              <br>
               <textarea rows="4" cols="50" type="text"
                     id="moreInfor"
                     class="form-control"
                     v-model="userData.more" placeholder="Màu sắc : đen , trắng, hồng mini + ghi chú"></textarea>
              <br>
              <div id="btnSubmit">
                <button type="button" class="btn btn-danger" @click.prevent="submitted">HOÀN TẤT ĐẶT HÀNG</button>
              </div>
            </div>
          </article>
        </div>
        <div class="col-12 col-md-3"></div>
      </div>
    </div>
  </section>
</template>

<script>
// eslint-disable-next-line
/* eslint-disable */

import SaleLayout from "../sale/sale-layout";
import CountdowLayout from "../countdown/countdow-layout";
export default {
  name: 'form-layout',
  components: {
    CountdowLayout,
    SaleLayout
  },
  data: function () {
    return {
      userData: {
        name: '',
        phone: '',
        adress: '',
        count: '',
        more: ''
      }
    }
  },
  methods: {
    async submitted(submitEvent) {
      let valueName = this.userData.name.split(' ').join('++')
      let valueAdress = this.userData.adress.split(' ').join('++')
      let valueCount = this.userData.count.split(' ').join('++')
      let valueMore = this.userData.more.split(' ').join('++')
      fetch("https://script.google.com/macros/s/AKfycbxuaXtlqR-Gvr8yEejWTZc18FwpinN703OIiaPOZRT-CwFTwBU/exec?action=insert-data&name=" + valueName + "&phone=‘" + this.userData.phone + "&address=" + valueAdress + "&count=" + valueCount + "&more=" + valueMore, {
        "method": "GET"
      }).then(response => {
        if (response.ok) {
          alert('Bạn đã đặt hàng thành công! đơn hàng sẽ được xác nhận ngay')
        } else {
          alert("Server returned " + response.status + " : " + response.statusText)
        }
      }).then(response => {
        this.result = response.body
        this.responseAvailable = true
      }).catch(err => {
        console.log(err)
      });
    }
  }
}
</script>
<style scoped>
#btnSubmit{
  display: flex;
  justify-content: center;
}
h2{
  text-align: center;
  margin: 15px;
  color:black
}
section {
  padding: 25px 0px;
  background: #ccc;
}

#formCustomer {
  padding: 25px 50px;
  border-radius: 20px;
  background: #fff;
}
</style>



