<template>
  <div class="cmt__group">
    <img v-bind:src="cmtUserImage" alt="" class="img-fluid">
    <div class="text__cmt">
      <p><b class="cmt__user">{{cmtUserName}}</b><span class="cmt__time"> {{cmtDate}}</span><span class="start__cmt">    (5 *)   </span><span class="buyed"> Đã mua sản phẩm</span></p>
      <h5>{{cmtTitle}}</h5>
      <p>{{cmtContent}}</p>
    </div>
  </div>
</template>

<script>
// eslint-disable-next-line
/* eslint-disable */
export default {
  name: "comment-layout",
  props: {
    cmtUserImage: String,
    cmtUserName: String,
    cmtDate: String,
    cmtTitle: String,
    cmtContent: String
  }
}
</script>

<style scoped>
.start__cmt{
  color: red;
}
.buyed{
  color: rgb(8, 172, 95);
  font-weight: bold;
}
.cmt__group {
  margin: 40px 0px;
  width: 100%;
  display: flex;
  justify-content: center;
}

img {
  float: left;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.text__cmt {
  width: 70%;
  margin-left: 25px;
  float: left;
  background: #d8d6d6;
  padding: 15px;
  border-radius: 20px;
}
</style>



