<template>
  <div class="container">
    <div class="row">
      <div class="col-0 col-md-2"></div>
      <div class="col-12 col-md-8">
<div class="videoyoutube">
      <h2>THÔNG TIN SẢN PHẨM</h2>
      <div class="videoWrapper">
        <iframe width="560" height="315" src="https://www.youtube.com/embed/BkENttf5kHQ" frameborder="0"
                allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                allowfullscreen></iframe>
      </div>
    </div>
      </div>
      <div class="col-0 col-md-2"></div>
    </div>
  </div>
</template>

<script>
// eslint-disable-next-line
/* eslint-disable */
export default {
  name: "video-layout"
}
</script>

<style scoped>
.videoyoutube {
  margin: 25px;
}

.videoyoutube h2 {
  text-align: center;
}
.videoWrapper {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  padding-top: 25px;
  height: 0;
}
.videoWrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
</style>



